.task-container{
  width: 500px;
  min-height: 65px;
  background-color: #f59bad;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  border: 1px solid white;
  cursor: pointer;
  user-select: none;
}

.finished{
  text-decoration: line-through;
  background-color: #8aa1db;
}

.task-text{
  width: 100%;
  height: 100%;
  font-size: 20px;
  font-weight: bold;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
}

.task-icon{
  width: 25px;
  height: 25px;
}
